import React from "react";
import ProductBox from "./ProductBoxD/ProductBox";
import "./slick-list.css";
function SlickList(props) {
    return (
        <div className="slick-list">
            {
                props.data.map(data => (
                    <ProductBox
                        key={data.id}
                        data={data}
                        imgUrl={data.thumbnailUrl}
                        title={data.title}
                        cart={props.cart}
                        setCart={props.setCart}
                        counter={props.counter}
                        setCounter={props.setCounter}
                    />
                ))
            }
        </div>
    );
}

export default SlickList;