import React from "react";
import "./product-box.css";
import Image from "./ImageD/Image";
import CartButton from "./CardBtnD/CartButton";

const ProductBox = ({imgUrl, title, data, cart, setCart, counter, setCounter}) => {
    // Add Card
    const addCart = () => {
        let tempCart = [];
        tempCart = cart;
        tempCart.push(data);
        setCart(tempCart);
        console.log(counter);
        console.log(cart);
    };
    return(
        <div className="product-box">
            <Image imgUrl={imgUrl} />
            <div className="info">
                {title}
            </div>
            <CartButton addCart={addCart}/>
        </div>
    );
};

export default ProductBox;