import React, {useEffect, useState} from "react";
import './App.css';
import './components/SlickListD/slick-list.css';
import SlickList from "./components/SlickListD/SlickList";
import PrevButton from "./components/PrevButton";
import NextButton from "./components/NextButton";


function App() {
    // Basic
    const [data, setData] = useState([]);
    const [cart, setCart] = useState([]);
    const [counter, setCounter] = useState(0);
    // Hooks
    useEffect(() => {
        async function getData(){
            const res = await fetch("https://jsonplaceholder.typicode.com/photos");
            const data = await res.json();
            setData(data.filter(data => (data.id <= 50)));
        }
        getData();
    }, []);


    // Slide
    const productSlide = document.querySelector(".slick-list");
    let loc = 0;
    function forward(){
        if (loc !== (data.length/5 - 1)){
            loc++;
            console.log(loc + " Forward Change");
            productSlide.style.transition = "transform .6s ease-in-out";
            productSlide.style.transform = "translateX(" + (-75 * loc) + "em)";
            hidePrevBtn(loc);
            hideNextBtn(loc);
        }
    }
    function backward(){
        if (loc !== 0){
            loc--;
            console.log(loc + " Backward Change");
            productSlide.style.transition = "transform .6s ease-in-out";
            productSlide.style.transform = "translateX(" + (-75 * loc) + "em)";
            hideNextBtn(loc);
            hidePrevBtn(loc);
        }
    }
    function showLoc(){
        console.log(loc);
    }

    // Hiding Buttons
    const prevBtn = document.getElementById("prev");
    const nextBtn = document.getElementById("next");
    const hidePrevBtn = (e) => {
        return (e === 0 ? prevBtn.classList.add('hidden') : prevBtn.classList.remove("hidden"));
    };
    const hideNextBtn = (e) => {
      return (e === (data.length / 5 - 1) ? nextBtn.classList.add('hidden') : nextBtn.classList.remove("hidden"));
    };

    return (
        <div className="App">
            <div className="container">
                <PrevButton backward={backward} />
                <div className="slider-track">
                    <SlickList cart={cart} setCart={setCart} data={data} counter={counter} setCounter={setCounter} />
                </div>
                <NextButton forward={forward}  />
            </div>

            <div className="card-show-container">
                <div className="card-show-wrapper">
                    <i className="fa fa-shopping-cart" onClick={showLoc}  >

                    </i>
                </div>
            </div>
        </div>
    );
}

export default App;
